.header {
  width: 100%;
  height: 70px;
  background: #FFFFFF;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
}

.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  max-width: 1580px;
  margin: 0 auto;
  padding: 0 20px;
}

.logo {
  font-weight: 700;
  font-size: 2rem;
  transition: color .2s linear;
}

.logo:hover {
  color: rgb(0, 0, 133);
}

.featured {
  display: flex;
  align-items: center;
  font-size: 1.5rem;
  font-weight: 500;
  transition: color .2s linear;
}

.featured:hover:enabled {
  color: rgb(0, 0, 133);
}

.featured path {
  transition: fill .2s linear;
}

.featured:hover:enabled path {
  fill: red;
  /* color: rgba(0, 0, 0, 0); */
  transition: fill .2s linear;
}

.featured:disabled {
  cursor: auto;
}